.container {
	--w: 1330px;
	--p: 30px;
	max-width: var(--w);
	padding-left: var(--p);
	padding-right: var(--p);
	margin-right: auto;
	margin-left: auto;
	&-fluid {
		--w: 100%;
		--p: 80px; } }


.running-line {
	--mt: -86px;
	margin-top: var(--mt);
	z-index: 9;
	position: relative;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	& &__box {
		--fz: 140px;
		--time: 20s;
		font-size: var(--fz);
		color: #212121;
		display: inline-block;
		padding-left: 100vw;
		animation: runningText var(--time) infinite linear;
		white-space: nowrap;
		position: relative;
		&--transparent {
			color: transparent;
			.text-line {
				position: absolute;
				top: 0;
				left: 100vw;
				letter-spacing: 0.2375rem; } } } }

@keyframes runningText {
	0% {
		transform: translate(0, 0); }
	100% {
		transform: translate(-100%, 0); } }


.text-line {
	--w: 100%;
	--h: 100%;
	--color: #000000;
	--border: 2px;
	--fw: 700;
	--fz: 8.75rem;
	width: var(--w);
	height: var(--h);
	stroke: var(--color);
	stroke-width: var(--border);
	font-weight: var(--fw);
	font-size: var(--fz);
	display: flex;
	&-with-ghost {
		position: relative;
		color: transparent;
		--fz-ghost: 30px;
		font-size: var(--fz-ghost);
		.text-line {
			--t: 0;
			--l: 0;
			position: absolute;
			top: var(--t);
			left: var(--l);
			font-size: var(--fz-ghost); } } }


@keyframes dash {
	to {
		stroke-dashoffset: 0; } }


@keyframes filling {
	from {
		fill: #2A4A73;
		fill-opacity: 0; }

	to {
		fill: #2A4A73;
		fill-opacity: 1; } }






.header {
	background-color: var(--gray-light);
	min-height: 67.5rem; }



.header-slide {
	padding-top: 9.125rem;
	&__meta {
		margin: 0; }
	&__title {
		font-size: 6.5625rem;
		margin: 0;
		line-height: 1.25;
		margin-bottom: 2.625rem;
		font-weight: 400;
		letter-spacing: 0.09375rem; }
	&__btn {} }




.block-title {
	font-size: 5.75rem;
	font-weight: 700;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	& &__icon {
		line-height: 1;
		.text-line {
			--border: 0.0625rem;
			--color: var(--pr);
			--fz: 2.25rem; } }
	& &__text {
		margin-left: -0.25rem;
		letter-spacing: -0.0625rem;
		&-line {
			--fz-ghost: 5.75rem;
			margin-top: -0.125rem;
			margin-left: -0.3125rem;
			letter-spacing: -0.0625rem;
			.text-line {
				--border: 1px; } } } }






.partners {
	padding: 13.25rem 0 0;
	& &__box {
		justify-content: space-between; }
	& &__text {}
	& &__btn-holder {
		align-self: flex-end;
		margin-bottom: 2.25rem; } }
